'use strict';

// MODULES //

import isLittleEndian from '@stdlib/assert-is-little-endian';


// MAIN //

var LOW;
if ( isLittleEndian === true ) {
	LOW = 0; // first index
} else {
	LOW = 1; // second index
}


// EXPORTS //

export default LOW;
